export function deepClone(origin, target) {
  var target = target || {}
  let toString = Object.prototype.toString
  let arrStr = '[object Array]'
  for (let item in origin) {
    if(origin.hasOwnProperty(item)) {
      if (typeof(origin[item]) === 'object' &&
          origin[item] !== 'null') {
        target[item] = toString.call(origin[item]) === arrStr? [] : {}
        deepClone(origin[item], target[item])
      }else{
        target[item] = origin[item];
    } 
  }
 
}
return target
}

 